.order-table {
  width: 100%;
  table-layout: fixed;
  text-align: right;
  border-collapse: collapse;
  font-size: 1.4rem; }
  .order-table th {
    padding: 2px 4px; }
  .order-table caption,
  .order-table thead.is-hide {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden; }
  .order-table tbody tr:nth-child(2n - 1) td {
    background-color: #eee; }
  .order-table tbody tr:hover td {
    background: transparent; }
  .order-table tbody tr:hover td:before {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: currentColor;
    opacity: 0.1;
    content: ''; }
  .order-table tbody td {
    position: relative;
    border: 1px solid #ccc; }
  .order-table tbody button {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 2px 4px;
    border: 0;
    background: transparent;
    text-align: right;
    font-size: 1.4rem;
    color: #000; }
  .order-table tbody .depth {
    position: absolute;
    top: 2px;
    bottom: 2px;
    left: 0;
    opacity: 0.2;
    transition: width 0.1s ease-in-out; }
    .order-table tbody .depth span {
      position: absolute;
      clip: rect(0 0 0 0);
      width: 1px;
      height: 1px;
      margin: -1px;
      overflow: hidden; }



