.price-board {
  display: block;
  text-align: center; }
  .price-board .price-board__last-price {
    font-size: 2rem;
    line-height: 1.2; }
    .price-board .price-board__last-price svg {
      vertical-align: top; }
    .price-board .price-board__last-price.PlusTick, .price-board .price-board__last-price.ZeroPlusTick {
      color: #3e8654; }
      .price-board .price-board__last-price.PlusTick svg, .price-board .price-board__last-price.ZeroPlusTick svg {
        fill: currentColor; }
    .price-board .price-board__last-price.MinusTick, .price-board .price-board__last-price.ZeroMinusTick {
      color: #d16547; }
      .price-board .price-board__last-price.MinusTick svg, .price-board .price-board__last-price.ZeroMinusTick svg {
        fill: currentColor; }
  .price-board .price-board__index-price {
    color: #666; }
  .price-board .price-board__fair-price {
    color: #000; }
  .price-board .price-board__slash {
    margin: 0 0.5rem;
    color: #aaa; }



